import { useMemo } from "react";
import PropTypes from "prop-types";
import styles from "./GroupComponent1.module.css";

const GroupComponent1 = ({
  className = "",
  medanBottom,
  medan,
  mobileLegendTournamentPol,
  medanLinkIcon,
  propAlignSelf,
  propFlex,
  propHeight,
}) => {
  const groupDiv1Style = useMemo(() => {
    return {
      alignSelf: propAlignSelf,
      flex: propFlex,
      height: propHeight,
    };
  }, [propAlignSelf, propFlex, propHeight]);

  return (
    <div
      className={[styles.medanTopParent, className].join(" ")}
      style={groupDiv1Style}
    >
      <div className={styles.medanTop} />
      <img
        className={styles.medanBottomIcon}
        loading="lazy"
        alt=""
        src={medanBottom}
      />
      <div className={styles.medanContent}>
        <div className={styles.medanTitle}>
          <div className={styles.medanCity}>
            <div className={styles.medan}>{medan}</div>
          </div>
          <div className={styles.medanTournament}>
            <b className={styles.mobileLegendTournament}>
              {mobileLegendTournamentPol}
            </b>
            <div className={styles.medanLink}>
              <img
                className={styles.medanLinkIcon}
                loading="lazy"
                alt=""
                src={medanLinkIcon}
              />
              <button className={styles.medanLinkLabel}>
                <div className={styles.medanLinkLabelChild} />
                <div className={styles.readMore}>Read More</div>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

GroupComponent1.propTypes = {
  className: PropTypes.string,
  medanBottom: PropTypes.string,
  medan: PropTypes.string,
  mobileLegendTournamentPol: PropTypes.string,
  medanLinkIcon: PropTypes.string,

  /** Style props */
  propAlignSelf: PropTypes.any,
  propFlex: PropTypes.any,
  propHeight: PropTypes.any,
};

export default GroupComponent1;
