import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./GameHighlights.module.css";

const GameHighlights = ({ className = "" }) => {
  const navigate = useNavigate();

  const onCarouselItemsImageClick = useCallback(() => {
    navigate("/maps");
  }, [navigate]);

  return (
    <section className={[styles.gameHighlights, className].join(" ")}>
      <div className={styles.highlightContent}>
        <div className={styles.imageCarousel}>
          <img
            className={styles.carouselItemsIcon}
            loading="lazy"
            alt=""
            src="/rectangle-4737@2x.png"
          />
          <img
            className={styles.carouselItemsIcon1}
            loading="lazy"
            alt=""
            src="/rectangle-4738@2x.png"
            onClick={onCarouselItemsImageClick}
          />
          <img
            className={styles.imageCarouselChild}
            loading="lazy"
            alt=""
            src="/rectangle-4739@2x.png"
          />
        </div>
        <div className={styles.gameTitles}>
          <div className={styles.popularGames}>
            <div className={styles.otherGames}>
              <h1 className={styles.freeFire}>Free fire</h1>
            </div>
            <h1 className={styles.pubg}>PUBG</h1>
            <h1 className={styles.valorant}>Valorant</h1>
          </div>
        </div>
      </div>
    </section>
  );
};

GameHighlights.propTypes = {
  className: PropTypes.string,
};

export default GameHighlights;
