import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./FrameComponent15.module.css";

const FrameComponent15 = ({ className = "" }) => {
  const navigate = useNavigate();

  const onChatBubbleImageClick = useCallback(() => {
    navigate("/chat");
  }, [navigate]);

  const onEllipseImageClick = useCallback(() => {
    navigate("/profile");
  }, [navigate]);

  return (
    <section className={[styles.leftColumnWrapper, className].join(" ")}>
      <div className={styles.leftColumn}>
        <div className={styles.nestNerdWrapper}>
          <h1 className={styles.nestNerd}>
            <span>{`Nest `}</span>
            <span className={styles.nerd}>NERD</span>
          </h1>
        </div>
        <div className={styles.frameParent}>
          <div className={styles.frameWrapper}>
            <div className={styles.rectangleParent}>
              <div className={styles.frameChild} />
              <img
                className={styles.vectorIcon}
                loading="lazy"
                alt=""
                src="/vector.svg"
              />
            </div>
          </div>
          <img
            className={styles.chatbubbleIcon}
            loading="lazy"
            alt=""
            src="/chatbubble@2x.png"
            onClick={onChatBubbleImageClick}
          />
          <img
            className={styles.ellipseIcon}
            loading="lazy"
            alt=""
            src="/ellipse@2x.png"
            onClick={onEllipseImageClick}
          />
        </div>
      </div>
    </section>
  );
};

FrameComponent15.propTypes = {
  className: PropTypes.string,
};

export default FrameComponent15;
