import PropTypes from "prop-types";
import styles from "./RectangleComponent3.module.css";

const RectangleComponent3 = ({ className = "" }) => {
  return (
    <div className={[styles.rectangleWrapper, className].join(" ")}>
      <div className={styles.componentChild} />
    </div>
  );
};

RectangleComponent3.propTypes = {
  className: PropTypes.string,
};

export default RectangleComponent3;
