import { useMemo } from "react";
import PropTypes from "prop-types";
import styles from "./ErangelDescription.module.css";

const ErangelDescription = ({
  className = "",
  erangelContainer,
  erangelPetaBesarPertempur,
  erangle,
  propPadding,
}) => {
  const erangelDescriptionStyle = useMemo(() => {
    return {
      padding: propPadding,
    };
  }, [propPadding]);

  return (
    <div
      className={[styles.erangelDescription, className].join(" ")}
      style={erangelDescriptionStyle}
    >
      <img
        className={styles.erangelContainerIcon}
        loading="lazy"
        alt=""
        src={erangelContainer}
      />
      <div className={styles.erangelWrapper}>
        <div className={styles.erangelPetaBesar}>
          {erangelPetaBesarPertempur}
        </div>
        <div className={styles.erangelTitleContainer}>
          <h3 className={styles.erangle}>{erangle}</h3>
        </div>
      </div>
    </div>
  );
};

ErangelDescription.propTypes = {
  className: PropTypes.string,
  erangelContainer: PropTypes.string,
  erangelPetaBesarPertempur: PropTypes.string,
  erangle: PropTypes.string,

  /** Style props */
  propPadding: PropTypes.any,
};

export default ErangelDescription;
