import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./LeftPane.module.css";

const LeftPane = ({ className = "" }) => {
  const navigate = useNavigate();

  const onChatBubbleImageClick = useCallback(() => {
    navigate("/chat");
  }, [navigate]);

  const onEllipseImageClick = useCallback(() => {
    navigate("/profile");
  }, [navigate]);

  return (
    <header className={[styles.leftPane, className].join(" ")}>
      <div className={styles.frameParent}>
        <div className={styles.nestNerdWrapper}>
          <h1 className={styles.nestNerd}>
            <span>{`Nest `}</span>
            <span className={styles.nerd}>NERD</span>
          </h1>
        </div>
        <div className={styles.frameGroup}>
          <div className={styles.frameWrapper}>
            <div className={styles.frameContainer}>
              <div className={styles.allParent}>
                <a className={styles.all}>All</a>
                <div className={styles.polygonWrapper}>
                  <img
                    className={styles.frameChild}
                    alt=""
                    src="/polygon-21.svg"
                  />
                </div>
              </div>
              <div className={styles.rectangleParent}>
                <div className={styles.frameItem} />
                <div className={styles.vectorParent}>
                  <img
                    className={styles.vectorIcon}
                    alt=""
                    src="/vector1.svg"
                  />
                  <div className={styles.rectangleGroup}>
                    <div className={styles.frameInner} />
                    <img
                      className={styles.vectorIcon1}
                      alt=""
                      src="/vector1.svg"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <img
            className={styles.chatBubbleIcon}
            loading="lazy"
            alt=""
            src="/chatbubble@2x.png"
            onClick={onChatBubbleImageClick}
          />
          <img
            className={styles.ellipseIcon}
            loading="lazy"
            alt=""
            src="/ellipse1@2x.png"
            onClick={onEllipseImageClick}
          />
        </div>
      </div>
    </header>
  );
};

LeftPane.propTypes = {
  className: PropTypes.string,
};

export default LeftPane;
