import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./PostFooter.module.css";

const PostFooter = ({ className = "" }) => {
  const navigate = useNavigate();

  const onVectorIconClick = useCallback(() => {
    navigate("/share");
  }, [navigate]);

  return (
    <div className={[styles.postFooter, className].join(" ")}>
      <div className={styles.footerContent}>
        <img
          className={styles.footerRectIcon}
          loading="lazy"
          alt=""
          src="/rectangle-52@2x.png"
        />
        <div className={styles.timeInfo}>
          <div className={styles.timeContent}>
            <div className={styles.minutesAgo}>45 minutes ago</div>
            <div className={styles.timeContentInner}>
              <div className={styles.vectorParent}>
                <img className={styles.vectorIcon} alt="" src="/vector-4.svg" />
                <input className={styles.frameInput} type="checkbox" />
                <img
                  className={styles.vectorIcon1}
                  alt=""
                  src="/vector-61.svg"
                  onClick={onVectorIconClick}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

PostFooter.propTypes = {
  className: PropTypes.string,
};

export default PostFooter;
