import { useCallback } from "react";
import FrameComponent7 from "./FrameComponent7";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./Footer.module.css";

const Footer = ({ className = "" }) => {
  const navigate = useNavigate();

  const onHomeTextClick = useCallback(() => {
    navigate("/");
  }, [navigate]);

  const onComunityTextClick = useCallback(() => {
    navigate("/community");
  }, [navigate]);

  const onNewsTextClick = useCallback(() => {
    navigate("/news");
  }, [navigate]);

  const onForumTextClick = useCallback(() => {
    navigate("/forum");
  }, [navigate]);

  return (
    <header className={[styles.footer, className].join(" ")}>
      <FrameComponent7 vector="/vector2.svg" />
      <div className={styles.navigation}>
        <div className={styles.navigationItems}>
          <a className={styles.home} onClick={onHomeTextClick}>
            Home
          </a>
        </div>
        <div className={styles.navigationItems1}>
          <a className={styles.comunity} onClick={onComunityTextClick}>
            Comunity
          </a>
        </div>
        <div className={styles.navigationItems2}>
          <a className={styles.news} onClick={onNewsTextClick}>
            News
          </a>
        </div>
        <div className={styles.frameParent}>
          <div className={styles.forumWrapper}>
            <a className={styles.forum} onClick={onForumTextClick}>
              Forum
            </a>
          </div>
          <div className={styles.games}>
            <div className={styles.games1}>Games</div>
            <div className={styles.gamesInner}>
              <img
                className={styles.frameChild}
                loading="lazy"
                alt=""
                src="/polygon-2.svg"
              />
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};

Footer.propTypes = {
  className: PropTypes.string,
};

export default Footer;
