import { useMemo, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./FrameComponent3.module.css";

const FrameComponent3 = ({
  className = "",
  ellipse,
  frameDivWidth,
  onNewsTextClick,
}) => {
  const frameHeaderStyle = useMemo(() => {
    return {
      width: frameDivWidth,
    };
  }, [frameDivWidth]);

  const navigate = useNavigate();

  const onChatBubbleImageClick = useCallback(() => {
    navigate("/chat");
  }, [navigate]);

  const onEllipseImageClick = useCallback(() => {
    navigate("/profile");
  }, [navigate]);

  const onHomeTextClick = useCallback(() => {
    navigate("/");
  }, [navigate]);

  const onComunityTextClick = useCallback(() => {
    navigate("/community");
  }, [navigate]);

  const onForumTextClick = useCallback(() => {
    navigate("/forum");
  }, [navigate]);

  return (
    <header
      className={[styles.frameWrapper, className].join(" ")}
      style={frameHeaderStyle}
    >
      <div className={styles.frameParent}>
        <div className={styles.frameGroup}>
          <div className={styles.nestNerdWrapper}>
            <h1 className={styles.nestNerd}>
              <span>{`Nest `}</span>
              <span className={styles.nerd}>NERD</span>
            </h1>
          </div>
          <div className={styles.frameContainer}>
            <div className={styles.frameDiv}>
              <div className={styles.rectangleParent}>
                <div className={styles.frameChild} />
                <img
                  className={styles.vectorIcon}
                  loading="lazy"
                  alt=""
                  src="/vector1.svg"
                />
              </div>
            </div>
            <div className={styles.chatBubbleWrapper}>
              <img
                className={styles.chatBubbleIcon}
                loading="lazy"
                alt=""
                src="/chatbubble@2x.png"
                onClick={onChatBubbleImageClick}
              />
            </div>
            <img
              className={styles.ellipseIcon}
              loading="lazy"
              alt=""
              src={ellipse}
              onClick={onEllipseImageClick}
            />
          </div>
        </div>
        <div className={styles.menuOptionsParent}>
          <div className={styles.menuOptions}>
            <a className={styles.home} onClick={onHomeTextClick}>
              Home
            </a>
          </div>
          <div className={styles.menuOptions1}>
            <div className={styles.comunity} onClick={onComunityTextClick}>
              Comunity
            </div>
          </div>
          <div className={styles.menuOptions2}>
            <a className={styles.news} onClick={onNewsTextClick}>
              News
            </a>
          </div>
          <div className={styles.frameParent1}>
            <div className={styles.forumWrapper}>
              <a className={styles.forum} onClick={onForumTextClick}>
                Forum
              </a>
            </div>
            <div className={styles.games}>
              <div className={styles.games1}>Games</div>
              <div className={styles.gamesInner}>
                <img
                  className={styles.frameItem}
                  loading="lazy"
                  alt=""
                  src="/polygon-2.svg"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};

FrameComponent3.propTypes = {
  className: PropTypes.string,
  ellipse: PropTypes.string,

  /** Style props */
  frameDivWidth: PropTypes.any,

  /** Action props */
  onNewsTextClick: PropTypes.func,
};

export default FrameComponent3;
