import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import FrameComponent7 from "./FrameComponent7";
import PropTypes from "prop-types";
import styles from "./FrameComponent6.module.css";

const FrameComponent6 = ({ className = "" }) => {
  const navigate = useNavigate();

  const onNestNERDTextClick = useCallback(() => {
    navigate("/");
  }, [navigate]);

  const onComunityTextClick = useCallback(() => {
    navigate("/community");
  }, [navigate]);

  const onNewsTextClick = useCallback(() => {
    navigate("/news");
  }, [navigate]);

  const onForumTextClick = useCallback(() => {
    navigate("/forum");
  }, [navigate]);

  return (
    <div className={[styles.frameWrapper, className].join(" ")}>
      <div className={styles.frameParent}>
        <header className={styles.frameGroup}>
          <FrameComponent7
            vector="/vector1.svg"
            onNestNERDTextClick={onNestNERDTextClick}
          />
          <div className={styles.newsForum}>
            <div className={styles.homeComunityWrapper}>
              <div className={styles.homeComunity}>
                <a className={styles.home} onClick={onNestNERDTextClick}>
                  Home
                </a>
                <div className={styles.comunity} onClick={onComunityTextClick}>
                  Comunity
                </div>
              </div>
            </div>
            <div className={styles.newsContent}>
              <a className={styles.news} onClick={onNewsTextClick}>
                News
              </a>
            </div>
            <div className={styles.newsContent1}>
              <a className={styles.forum} onClick={onForumTextClick}>
                Forum
              </a>
            </div>
            <div className={styles.games}>
              <div className={styles.games1}>Games</div>
              <div className={styles.gamesInner}>
                <img
                  className={styles.frameChild}
                  loading="lazy"
                  alt=""
                  src="/polygon-2.svg"
                />
              </div>
            </div>
          </div>
        </header>
        <div className={styles.frameContainer}>
          <div className={styles.frameDiv}>
            <button className={styles.rectangleParent}>
              <div className={styles.frameItem} />
              <div className={styles.twitter}>Twitter</div>
            </button>
            <button className={styles.rectangleGroup}>
              <div className={styles.frameInner} />
              <div className={styles.facebook}>Facebook</div>
            </button>
            <div className={styles.rectangleContainer}>
              <div className={styles.rectangleDiv} />
              <div className={styles.whatsaap}>Whatsaap</div>
            </div>
            <button className={styles.groupButton}>
              <div className={styles.frameChild1} />
              <div className={styles.tiktok}>Tiktok</div>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

FrameComponent6.propTypes = {
  className: PropTypes.string,
};

export default FrameComponent6;
