import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import FrameComponent3 from "./FrameComponent3";
import PropTypes from "prop-types";
import styles from "./FrameComponent19.module.css";

const FrameComponent19 = ({ className = "" }) => {
  const navigate = useNavigate();

  const onNewsTextClick = useCallback(() => {
    navigate("/news");
  }, [navigate]);

  return (
    <section className={[styles.newsItemParent, className].join(" ")}>
      <FrameComponent3
        ellipse="/ellipse1@2x.png"
        frameDivWidth="1411px"
        onNewsTextClick={onNewsTextClick}
      />
      <div className={styles.pubgBanner}>
        <img
          className={styles.pubgBannerChild}
          alt=""
          src="/rectangle-291@2x.png"
        />
        <h2 className={styles.pubgPlayerUnknown}>
          PUBG: player unknown battleground
        </h2>
      </div>
    </section>
  );
};

FrameComponent19.propTypes = {
  className: PropTypes.string,
};

export default FrameComponent19;
