import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./FrameComponent16.module.css";

const FrameComponent16 = ({ className = "" }) => {
  const navigate = useNavigate();

  const onComunityTextClick = useCallback(() => {
    navigate("/community");
  }, [navigate]);

  const onNewsTextClick = useCallback(() => {
    navigate("/news");
  }, [navigate]);

  const onForumTextClick = useCallback(() => {
    navigate("/forum");
  }, [navigate]);

  const onGroupContainerClick = useCallback(() => {
    const anchor = document.querySelector("[data-scroll-to='thoughtBox']");
    if (anchor) {
      anchor.scrollIntoView({ block: "start", behavior: "smooth" });
    }
  }, []);

  return (
    <div className={[styles.homeCommunityParent, className].join(" ")}>
      <div className={styles.homeCommunity}>
        <a className={styles.home}>Home</a>
      </div>
      <div className={styles.homeCommunity1}>
        <div className={styles.comunity} onClick={onComunityTextClick}>
          Comunity
        </div>
      </div>
      <div className={styles.frameParent}>
        <div className={styles.frameGroup}>
          <div className={styles.newsForumButtonParent}>
            <div className={styles.newsForumButton}>
              <a className={styles.news} onClick={onNewsTextClick}>
                News
              </a>
            </div>
            <div className={styles.newsForumButton1}>
              <a className={styles.forum} onClick={onForumTextClick}>
                Forum
              </a>
            </div>
            <div className={styles.games}>
              <div className={styles.games1}>Games</div>
              <div className={styles.gamesInner}>
                <img
                  className={styles.frameChild}
                  alt=""
                  src="/polygon-2.svg"
                />
              </div>
            </div>
          </div>
          <div className={styles.frameWrapper}>
            <div
              className={styles.thoughtBoxParent}
              onClick={onGroupContainerClick}
            >
              <div className={styles.thoughtBox} data-scroll-to="thoughtBox" />
              <div className={styles.whatAreYou}>
                What are you thinking about?
              </div>
            </div>
          </div>
        </div>
        <div className={styles.vectorWrapper}>
          <img className={styles.vectorIcon} alt="" src="/vector-32.svg" />
        </div>
      </div>
    </div>
  );
};

FrameComponent16.propTypes = {
  className: PropTypes.string,
};

export default FrameComponent16;
