import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./FrameComponent9.module.css";

const FrameComponent9 = ({
  className = "",
  hourAgo,
  firstIconShape,
  thirdIconShape,
  firstDot,
  secondDot,
  thirdDot,
}) => {
  const navigate = useNavigate();

  const onThirdIconShapeClick = useCallback(() => {
    navigate("/share");
  }, [navigate]);

  return (
    <div className={[styles.infoTitleInner, className].join(" ")}>
      <div className={styles.hourAgoParent}>
        <div className={styles.hourAgo}>{hourAgo}</div>
        <div className={styles.frameWrapper}>
          <div className={styles.frameParent}>
            <div className={styles.firstIconShapeParent}>
              <img
                className={styles.firstIconShape}
                alt=""
                src={firstIconShape}
              />
              <input className={styles.frameInput} type="checkbox" />
              <img
                className={styles.thirdIconShape}
                alt=""
                src={thirdIconShape}
                onClick={onThirdIconShapeClick}
              />
            </div>
            <div className={styles.frameContainer}>
              <div className={styles.firstDotParent}>
                <div className={styles.firstDot}>{firstDot}</div>
                <div className={styles.secondDot}>{secondDot}</div>
                <div className={styles.thirdDot}>{thirdDot}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

FrameComponent9.propTypes = {
  className: PropTypes.string,
  hourAgo: PropTypes.string,
  firstIconShape: PropTypes.string,
  thirdIconShape: PropTypes.string,
  firstDot: PropTypes.string,
  secondDot: PropTypes.string,
  thirdDot: PropTypes.string,
};

export default FrameComponent9;
