import { useMemo } from "react";
import PropTypes from "prop-types";
import styles from "./GroupComponent.module.css";

const GroupComponent = ({
  className = "",
  rectangle4733,
  mobileLegendTournamentPol,
  medan,
  propLeft,
}) => {
  const groupDivStyle = useMemo(() => {
    return {
      left: propLeft,
    };
  }, [propLeft]);

  return (
    <div
      className={[styles.rectangleParent, className].join(" ")}
      style={groupDivStyle}
    >
      <div className={styles.frameChild} />
      <img
        className={styles.frameItem}
        loading="lazy"
        alt=""
        src={rectangle4733}
      />
      <b className={styles.mobileLegendTournament}>
        {mobileLegendTournamentPol}
      </b>
      <img className={styles.vectorIcon} alt="" src="/vector-3.svg" />
      <button className={styles.tournamentReadMore}>
        <div className={styles.tournamentReadMoreChild} />
        <div className={styles.readMore}>Read More</div>
      </button>
      <div className={styles.medan}>{medan}</div>
    </div>
  );
};

GroupComponent.propTypes = {
  className: PropTypes.string,
  rectangle4733: PropTypes.string,
  mobileLegendTournamentPol: PropTypes.string,
  medan: PropTypes.string,

  /** Style props */
  propLeft: PropTypes.any,
};

export default GroupComponent;
