import { useMemo } from "react";
import PropTypes from "prop-types";
import styles from "./FrameComponent10.module.css";

const FrameComponent10 = ({
  className = "",
  ellipse,
  kimJiwon,
  propPadding,
  onEllipseImageClick1,
}) => {
  const frameDivStyle = useMemo(() => {
    return {
      padding: propPadding,
    };
  }, [propPadding]);

  return (
    <div
      className={[styles.ellipseParent, className].join(" ")}
      style={frameDivStyle}
    >
      <img
        className={styles.ellipseIcon}
        alt=""
        src={ellipse}
        onClick={onEllipseImageClick1}
      />
      <div className={styles.kimJiwonWrapper}>
        <h2 className={styles.kimJiwon}>{kimJiwon}</h2>
      </div>
    </div>
  );
};

FrameComponent10.propTypes = {
  className: PropTypes.string,
  ellipse: PropTypes.string,
  kimJiwon: PropTypes.string,

  /** Style props */
  propPadding: PropTypes.any,

  /** Action props */
  onEllipseImageClick1: PropTypes.func,
};

export default FrameComponent10;
