import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import styles from "./InnerContent.module.css";

const InnerContent = ({ className = "" }) => {
  const navigate = useNavigate();

  const onChatBubbleImageClick = useCallback(() => {
    navigate("/chat");
  }, [navigate]);

  const onEllipseImageClick = useCallback(() => {
    navigate("/profile");
  }, [navigate]);

  return (
    <div className={[styles.innerContent, className].join(" ")}>
      <div className={styles.contentElement}>
        <h1 className={styles.nestNerd}>
          <span>{`Nest `}</span>
          <span className={styles.nerd}>NERD</span>
        </h1>
      </div>
      <div className={styles.element}>
        <div className={styles.subElement}>
          <div className={styles.subContent}>
            <div className={styles.subItem}>
              <a className={styles.all}>All</a>
              <div className={styles.subItemInner}>
                <img
                  className={styles.frameChild}
                  alt=""
                  src="/polygon-21.svg"
                />
              </div>
            </div>
            <div className={styles.rectangleParent}>
              <div className={styles.frameItem} />
              <div className={styles.vectorParent}>
                <img
                  className={styles.vectorIcon}
                  loading="lazy"
                  alt=""
                  src="/vector1.svg"
                />
                <div className={styles.rectangleGroup}>
                  <div className={styles.frameInner} />
                  <img
                    className={styles.vectorIcon1}
                    alt=""
                    src="/vector1.svg"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.bubbleContainer}>
          <div className={styles.chatBubbleParent}>
            <img
              className={styles.chatBubbleIcon}
              loading="lazy"
              alt=""
              src="/chatbubble@2x.png"
              onClick={onChatBubbleImageClick}
            />
          </div>
          <img
            className={styles.ellipseIcon}
            loading="lazy"
            alt=""
            src="/ellipse1@2x.png"
            onClick={onEllipseImageClick}
          />
        </div>
      </div>
    </div>
  );
};

InnerContent.propTypes = {
  className: PropTypes.string,
};

export default InnerContent;
