import { useEffect } from "react";
import {
  Routes,
  Route,
  useNavigationType,
  useLocation,
} from "react-router-dom";
import Home from "./pages/Home";
import News from "./pages/News";
import Community from "./pages/Community";
import Games from "./pages/Games";
import Forum from "./pages/Forum";
import Profile from "./pages/Profile";
import Comment1 from "./pages/Comment1";
import Share from "./pages/Share";
import Chat from "./pages/Chat";
import Profile1 from "./pages/Profile1";
import Artikel from "./pages/Artikel";
import Maps from "./pages/Maps";
import Gameinfo from "./pages/Gameinfo";
import Weapon from "./pages/Weapon";
import Vahicles from "./pages/Vahicles";
import Arcade from "./pages/Arcade";
import Event1 from "./pages/Event1";
import Editprofile from "./pages/Editprofile";
import Editprofile1 from "./pages/Editprofile1";

function App() {
  const action = useNavigationType();
  const location = useLocation();
  const pathname = location.pathname;

  useEffect(() => {
    if (action !== "POP") {
      window.scrollTo(0, 0);
    }
  }, [action, pathname]);

  useEffect(() => {
    let title = "";
    let metaDescription = "";

    switch (pathname) {
      case "/":
        title = "";
        metaDescription = "";
        break;
      case "/news":
        title = "";
        metaDescription = "";
        break;
      case "/community":
        title = "";
        metaDescription = "";
        break;
      case "/games":
        title = "";
        metaDescription = "";
        break;
      case "/forum":
        title = "";
        metaDescription = "";
        break;
      case "/profile":
        title = "";
        metaDescription = "";
        break;
      case "/comment":
        title = "";
        metaDescription = "";
        break;
      case "/share":
        title = "";
        metaDescription = "";
        break;
      case "/chat":
        title = "";
        metaDescription = "";
        break;
      case "/profile2":
        title = "";
        metaDescription = "";
        break;
      case "/artikel":
        title = "";
        metaDescription = "";
        break;
      case "/maps":
        title = "";
        metaDescription = "";
        break;
      case "/gameinfo":
        title = "";
        metaDescription = "";
        break;
      case "/weapon":
        title = "";
        metaDescription = "";
        break;
      case "/vahicles":
        title = "";
        metaDescription = "";
        break;
      case "/arcade":
        title = "";
        metaDescription = "";
        break;
      case "/event":
        title = "";
        metaDescription = "";
        break;
      case "/editprofile":
        title = "";
        metaDescription = "";
        break;
      case "/editprofile2":
        title = "";
        metaDescription = "";
        break;
    }

    if (title) {
      document.title = title;
    }

    if (metaDescription) {
      const metaDescriptionTag = document.querySelector(
        'head > meta[name="description"]'
      );
      if (metaDescriptionTag) {
        metaDescriptionTag.content = metaDescription;
      }
    }
  }, [pathname]);

  return (
    <Routes>
      <Route path="/" element={<Home />} />
      <Route path="/news" element={<News />} />
      <Route path="/community" element={<Community />} />
      <Route path="/games" element={<Games />} />
      <Route path="/forum" element={<Forum />} />
      <Route path="/profile" element={<Profile />} />
      <Route path="/comment" element={<Comment1 />} />
      <Route path="/share" element={<Share />} />
      <Route path="/chat" element={<Chat />} />
      <Route path="/profile2" element={<Profile1 />} />
      <Route path="/artikel" element={<Artikel />} />
      <Route path="/maps" element={<Maps />} />
      <Route path="/gameinfo" element={<Gameinfo />} />
      <Route path="/weapon" element={<Weapon />} />
      <Route path="/vahicles" element={<Vahicles />} />
      <Route path="/arcade" element={<Arcade />} />
      <Route path="/event" element={<Event1 />} />
      <Route path="/editprofile" element={<Editprofile />} />
      <Route path="/editprofile2" element={<Editprofile1 />} />
    </Routes>
  );
}
export default App;
