import { useMemo } from "react";
import PropTypes from "prop-types";
import styles from "./UserInfo.module.css";

const UserInfo = ({ className = "", propGap }) => {
  const userInfoStyle = useMemo(() => {
    return {
      gap: propGap,
    };
  }, [propGap]);

  return (
    <div
      className={[styles.userInfo, className].join(" ")}
      style={userInfoStyle}
    >
      <img className={styles.ellipseIcon} alt="" src="/ellipse-2@2x.png" />
      <div className={styles.goYounJungWrapper}>
        <h2 className={styles.goYounJung}>Go youn jung</h2>
      </div>
    </div>
  );
};

UserInfo.propTypes = {
  className: PropTypes.string,

  /** Style props */
  propGap: PropTypes.any,
};

export default UserInfo;
