import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import FrameComponent21 from "../components/FrameComponent21";
import FrameComponent23 from "../components/FrameComponent23";
import GameUpdates from "../components/GameUpdates";
import SkinUpdateContent from "../components/SkinUpdateContent";
import NewFeatures from "../components/NewFeatures";
import FrameComponent from "../components/FrameComponent";
import styles from "./Arcade.module.css";

const Arcade = () => {
  const navigate = useNavigate();

  const onChatBubbleImageClick = useCallback(() => {
    navigate("/chat");
  }, [navigate]);

  const onButtonContainerClick = useCallback(() => {
    navigate("/event");
  }, [navigate]);

  return (
    <div className={styles.arcade}>
      <FrameComponent21 onChatBubbleImageClick={onChatBubbleImageClick} />
      <FrameComponent23
        onButtonContainerClick={onButtonContainerClick}
        onButtonContainerClick={onButtonContainerClick}
      />
      <GameUpdates />
      <section className={styles.newSkin}>
        <div className={styles.newSkinChild} />
        <SkinUpdateContent />
        <div className={styles.weaponAnniversary}>
          <img
            className={styles.weaponAnniversaryChild}
            loading="lazy"
            alt=""
            src="/rectangle-40@2x.png"
          />
          <div className={styles.weaponAnniversarySkinContainer}>
            <p className={styles.weaponAnniversarySkin}>
              Weapon Anniversary skin
            </p>
            <p className={styles.availableNow}>Available now !!</p>
          </div>
        </div>
        <NewFeatures />
        <img className={styles.vectorIcon} alt="" src="/vector-27.svg" />
      </section>
      <FrameComponent />
    </div>
  );
};

export default Arcade;
